import {useRef, useEffect} from 'react'
import ring from '../../images/ring.gif'
import camera from '../../images/camera.gif'
import gift from '../../images/gift.gif'
import wine from '../../images/wine.gif'
import heart from '../../images/heart.gif'


export const SliderSlide = (props) => {
    const {classes, SliderSlideDiv, el, SliderWrap, index, currentSlide, setCurrentSlide, wrapHacked, slidesLength} = props
    const showSlide = currentSlide === index

    const onHandleClick = () => {
        if (slidesLength === (index + 1)) {
            setCurrentSlide(0)
        } else {
            setCurrentSlide(currentSlide + 1)
        }
    }

    const onBackClick = () => {
        console.log(slidesLength)
        if (currentSlide === 0) {
            
            setCurrentSlide(slidesLength - 1)
        } else {
            setCurrentSlide(currentSlide - 1)
        }
    }
    
    return (
        <div className={`${classes.slider__slide} ${showSlide ? classes.slider__slide__active : ''}`} data-slide={index} ref={SliderSlideDiv}>
            <div className={`${classes.slider__wrap} ${wrapHacked ? classes.slider__wrap__hacked : ''}`} ref={SliderWrap}>
                <div className={classes.slider__back} style={{backgroundImage: `url(${el.photo})`}}></div>
            </div>
            <div className={classes.slider__inner} style={{backgroundImage: `url(${el.photo})`}}>
                {index === 5 ? <img src={gift} style={{marginTop: '300px'}}/> : ''}
                {index === 7 ? <img src={wine} style={{marginTop: '-30px'}}/> : ''}
                <div className={classes.slider__content} style={
                    index === 1 ? {marginTop: '40px', marginLeft: '100px'} :
                    index === 3 ? {marginTop: '100px', marginLeft: '-20px'} : 
                    index === 6 ? {marginTop: '170px', marginLeft: '190px'} : 
                    index === 9 ? {marginTop: '370px', marginLeft: '-40px'} : 
                    index === 10 ? {marginTop: '-120px', marginLeft: '600px'} : {}
                }>
                    <h1 style={
                        index === 5 ? {color: 'white'} :
                        index === 6 ? {color: 'black'} : 
                        index === 7 ? {color: 'white'} : {}
                    }>{el.text}</h1><a className={classes.go_to_next} onClick={onHandleClick} style={
                        index === 6 ? {color: 'black'} : {}
                    }>next</a>
                </div>
                <div className={classes.backButton} onClick={onBackClick}></div>
                {index === 1 ? <img src={ring} style={{marginLeft: '100px'}}/> : ''}
                {index === 3 ? <img src={camera}/> : ''}
                {index === 13 ? <img src={heart}/> : ''}
            </div>
        </div>
    )
}