import classes from './HomeStyles.module.css'
import { useForm } from "react-hook-form";
import { Button1 } from '../commonComponents/button1/button1';
import { useHistory } from 'react-router-dom';
import confetti from '../../images/confetti.gif'

import { styled } from '@mui/material/styles';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import { useState } from 'react';

const BootstrapButton = styled(Button)({
    fontSize: 18,
    padding: '6px 12px',
    color: 'white',

    lineHeight: 1.5,
    backgroundColor: 'rgb(49,230,186)',
    borderColor: 'rgb(49,230,186)',
    fontFamily: [
        'Caveat',
        'sans-serif'
    ].join(','),
    '&:hover': {
        boxShadow: '0 0 1px 0.3rem rgba(49,230,186,1)',
        // border: '1px solid',
        // backgroundColor: 'rgb(49,230,186)',
        backgroundColor: 'rgba(49,230,186,.5)',
        // backgroundColor: '#0069d9',
        // borderColor: '#0062cc',
        // boxShadow: 'none',
    },
    '&:active': {
        boxShadow: '0 0 1px 0.3rem rgba(49,230,186,1)',
        // boxShadow: 'none',
        backgroundColor: 'rgba(49,230,186,.5)',
        // borderColor: 'rgb(49,230,186)',
        // backgroundColor: '#0062cc',
        // borderColor: '#005cbf',
    },
    // '&:focus': {
    //     boxShadow: '0 0 0 0.2rem rgba(0,123,255,.5)',
    // },
});

const CssTextField = styled(TextField)({
    '& label.Mui-focused': {
        fontFamily: [
            'Caveat',
            'sans-serif'
        ],
        color: 'black',
    },
    '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
    },
    '& .MuiOutlinedInput-root': {
        '& fieldset': {
            border: '3px solid',
            borderColor: 'rgb(211,81,139)',
        },
        '&:hover fieldset': {
            borderColor: 'rgb(49,230,186)',
        },
        '&.Mui-focused fieldset': {
            borderColor: 'rgb(49,230,186)',
        },
    },
});



export const Home = () => {
    const { register, handleSubmit } = useForm()
    const [inputState, setInputState] = useState('')
    const [error, setError] = useState(false)
    const history = useHistory()
    const onSubmit = data => {
        if (data.inputData === 'Мария') {
            history.push('/photos')
        } else {
            setError(true)
        }
    };
    const onHandleChange = (event) => {
        setInputState(event.target.value)
    }
    const onHandleClick = () => {
        if (inputState === 'Мария') {
            history.push('/photos')
        } else {
            setError(true)
        }
    }
    const onHandleClose = () => {
        setError(false)
    }
    return (
        // <div className={classes.wrapper}>
        //     <div className={classes.backgroundImage}></div>
        //     <div className={classes.content}>
        //         <div>Это открытка позравление</div>
        //         <form onSubmit={handleSubmit(onSubmit)} className={classes.form}>
        //             <input {...register("inputData")} className={classes.input} autoComplete='off'/>
        //             <Button1>Войти</Button1>
        //         </form>
        //     </div>
        // </div>
        <div className={classes.wrapper}>
            <div className={classes.backgroundImage}></div>
            <div className={classes.content}>
                <div className={classes.leftSide}>
                    <div>Эта открытка-поздравление для милой именинницы!</div>
                    <img src={confetti} />
                </div>
                <div className={classes.rightSide}>

                    <div className={classes.rightSideInput}>
                        <CssTextField onChange={onHandleChange} id="outlined-basic" label="Ваше имечко" variant="outlined" InputLabelProps={{
                            style: {
                                fontFamily: [
                                    'Caveat',
                                    'sans-serif'
                                ],
                                fontWeight: 500,
                                fontSize: 21
                            }
                        }} inputProps={{ style: { fontSize: 21 } }} />
                    </div>

                    <BootstrapButton onClick={onHandleClick}>Let's go</BootstrapButton>

                </div>
            </div>
            <Snackbar open={error} autoHideDuration={6000} onClose={onHandleClose} anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}>
                <MuiAlert  onClose={onHandleClose} severity="error" sx={{ width: '100%' }}>
                    {inputState === 'Маша' ? 'Не Маша, а Мария, пожалуйста)' : 'Эй, это не Мария, хватит меня обманывать!'}
                </MuiAlert >
            </Snackbar>
        </div>
    )
}